@import "assets/styles/mixin.scss";

.com-legalPage {
  // min-height: 100vh;
  padding: 50px 2rem;
  margin-top: 10vh;
  a.underline {
    color: #ff3050;
    text-decoration: underline;
    cursor: pointer;
  }
  li{
    // line-height: 1.5;
    margin-bottom: 0.75rem;
  }
  .title {
    @include title();
    font-size: 2rem;
    margin-bottom: 1rem;
  }
  .wrapper {
    .block-legal {
      font-size: 13px;
      .intro {
        margin-bottom: 1rem;
      }
      .item {
        margin-top: 2rem;
        .title {
          @include title();
          font-size: 1.25rem;
          margin-bottom: 0.75rem;
        }
        .header {
          @include title();
          text-transform: unset;
          margin-bottom: 0.75rem;
          margin-top: 0.75rem;
        }
        .content {
          margin-bottom: 1rem;
        }
        ul {
          padding-left: 1.5rem;
          list-style-type: unset;
          li {
          }
        }
        .bold {
          font-weight: bold;
        }
        // a.underline {
        //   color: #ff3050;
        //   text-decoration: underline;
        //   cursor: pointer;
        // }
        &.first {
          margin-top: 3rem;
        }
      }
    }
  }
}
@include desktop {
  .com-legalPage {
    min-height: 70vh;
    margin: auto;
    max-width: 900px;
    @include desktop2k{
      max-width: 1600px;
    }
    .title {
      @include title();
      font-size: 60px;
      margin-bottom: 2rem;
      @include desktop2k{
        font-size: 5rem;
        margin: 100px auto;
      }
    }
    .wrapper {
      margin-left: 170px;
      .block-legal {
        font-size: 18px;
        @include desktop2k{
          font-size: 1.35rem;
        }
        .intro {
          font-size: 20px;
          margin-bottom: 1.5rem;
        }
        .item {
          margin-top: 2rem;
          .title {
            font-size: 30px;
            margin-bottom: 1rem;
          }
          .header {
            @include title();
            text-transform: unset;
            margin-bottom: 0.5rem;
            margin-top: 0.5rem;
          }
          .content {
            margin-bottom: 1.5rem;
          }
          ul {
            padding-left: 2rem;
          }

        }
      }
    }
  }
}
